.experiences-list {
  text-align: justify;
  display: flex;
  flex-direction: column;
  justify-content: left;
  margin-right: 20%;
  margin-left: 20%;
  margin-bottom: 40px;
  align-items: center;
}

.experience-header {
  display: flex;
  justify-content: left;
  align-items: baseline;
}

.experience-tech-icon {
  width: 50px;
  height: 50px;
  margin: 10px;
  padding: 10px;
  // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);

}

.experience-tech-icon:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  // grow
  transform: scale(1.1);
  // transition
  transition: 0.3s;
}

.experience-techs {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.experience-detail {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;

  margin-top: 30px;
  padding: 40px;
}