.tech-list__container {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  max-width: 1200px;
  padding: 0 20px;
}

.tech-list__heading {
  font-weight: bold;
  font-size: 1.5em;
  margin-top: 40px; // Added margin-top for spacing above the heading
  margin-bottom: 40px; // Kept margin-bottom for spacing below the heading
}

.tech-card__container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  width: 100%;
  justify-content: center;
}

.tech-card__icon {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

.tech-card__link {
  text-decoration: none;
  color: inherit;
  width: calc(25% - 20px);
  min-width: 150px;
  max-width: 200px;
}

.tech-card__body {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 150px;
  background-color: #ffffff;
  border-radius: 8px;
  padding: 20px;
}

.tech-card__link:hover .tech-card__body {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transform: scale(1.05);
}

.tech-card__content {
  font-size: 1em;
  text-align: center;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .tech-card__link {
    width: calc(50% - 20px);
  }
}

@media (max-width: 480px) {
  .tech-card__link {
    width: 100%;
  }
}