.profile-picture {
  border-radius: 50%;
  width: 150px;
  margin: 10px;
}

.profile-visual {
  flex-direction: column;
  margin: 50px 50px 20px 20px;
}

.profile-links-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.fa {
  padding: 10px;
  font-size: 20px;
  margin: 2px;
  text-align: center;
  text-decoration: none;
  background: #4b4b4b;
  color: white;
  border-radius: 50%; // Added rounded corners for icons
  transition: background 0.3s ease, color 0.3s ease; // Smooth hover transition
}

.fa:hover {
  opacity: 0.7;
  color: black;
  background: #f0f0f0; // Added background change on hover
}

.profile-summary {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px 20px 20px 50px;
}

.profile-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; // Added wrap for better responsiveness
}

.profile-title {
  font-size: xx-large;
  font-weight: bolder;
  border-bottom: 2px solid black;
}

.profile-subtitle {
  font-size: x-large;
  font-weight: lighter;
}

.profile-about {
  align-items: center;
  justify-content: center;
  text-align: justify; // Changed from "justify"
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px auto;
  padding: 40px;
  max-width: 800px;
  width: 100%;
  border-radius: 8px;
  color: #333; // Added


  i {
    font-style: italic;
    color: #666;

    &::before,
    &::after {
      color: #999;
    }
  }

  @media (max-width: 768px) {
    font-size: 16px;
    padding: 20px;

    p {
      line-height: 1.5;
    }
  }
}